const Placeholder = () => {
  const context = useContext(interactionContext);

  const { panelWidth = 250, panelHeight = 250 } = context.props;

  return (
    <div
      css={css`
        width: ${panelWidth}px;
        height: ${panelHeight}px;
        margin: 10px;
        background: transparent;
        border: 2px solid transparent;
      `}
    />
  );
};

const Matching = props => {
  const { id, children, reset } = props as any;
  const MatchDrags = children[0].props.children;
  const MatchDrops = children[1].props.children;
  const Feedbacks = children[2].props.children;

  const context: any = useContext(interactionContext);

  const [feedback, setFeedback] = useState<any>(null);

  const itemsSeen = useRef(unlockInteraction(context.id, MatchDrags.length));

  const [columns, setColumns] = useState({
    column_a: window.dev()
      ? MatchDrags
      : MatchDrags?.length > 1
      ? shuffle(MatchDrags)
      : MatchDrags,
    column_b: MatchDrops,
  });

  const matchingDragIdToDropIdMap = MatchDrags.reduce((acc, curr, index) => {
    return {
      ...acc,
      [curr?.props?.id]: MatchDrops[index]?.props?.id,
    };
  }, {});

  const onDragStart = data => {
    // console.log(data);
  };

  const onDragEnd = data => {
    const { active, collisions, over } = data;

    if (!collisions?.length) {
      return;
    }

    const { id: dragId } = active;
    const { id: dropId } = over;

    const { column_a, column_b } = columns;

    if (matchingDragIdToDropIdMap[dragId] !== dropId) {
      return;
    }

    const dragIndex = column_a.findIndex((drag: any) => {
      return drag.props.id === dragId;
    });

    const dropIndex = column_b.findIndex((drop: any) => {
      return drop.props.id === dropId;
    });

    // add the drag to column_b
    const updatedColumnB = [...column_b];
    updatedColumnB[dropIndex] = column_a[dragIndex];

    // update columns
    const updatedColumns = {
      column_a: column_a.map((d, index) =>
        updatedColumnB.includes(d) ? (
          <Placeholder key={`placeholder_${index}`} />
        ) : (
          d
        )
      ),
      column_b: updatedColumnB.map((entry, index) =>
        index === dropIndex
          ? React.cloneElement(entry, {
              isDragDisabled: true,
            })
          : entry
      ),
    };

    setFeedback(Feedbacks[dropIndex]);
    setColumns(updatedColumns);
  };

  const idDep = MatchDrags.map(entry => {
    return entry.props.id;
  }).join('_');

  useEffect(() => {
    if (!window.dev()) {
      return;
    }

    setColumns({
      column_a: MatchDrags,
      column_b: MatchDrops,
    });
  }, [
    // idDep,
    MatchDrags,
    MatchDrops,
    Feedbacks,
  ]);

  useEffect(() => {
    if (window.dev()) return;

    if (context.props.lockDown === 0) return;
    feedback && itemsSeen.current(feedback.props.id);
  });

  useEffect(() => {
    const pS = pubSub.subscribe(
      `MATCHING_INTERACTION_FEEDBACK_${context.id}`,
      ({ eventType, index }) => {
        switch (eventType) {
          case 'click': {
            setFeedback(Feedbacks[index]);
            return;
          }
        }
      }
    );

    return () => pS();
  }, [Feedbacks]);

  return (
    <Layout
      key={window.dev() ? idDep : undefined}
      id={id}
      columns={columns}
      onDragStart={onDragStart}
      onDragEnd={onDragEnd}
      feedback={reRenderedFeedback(feedback)}
      useMediaInFeedback={context.props.useMediaInFeedback}
      setFeedback={setFeedback}
      reset={reset}
    />
  );
};

export const MatchingWrapper = ({ children, ...props }) => {
  const [reset, setReset] = useState<number>(0);

  return (
    <_
      key={`Matching_${children[0].props.children.length}${reset &&
        `_${reset}`}`}
    >
      <Matching children={children} {...{ ...props, reset }} />
      {window.dev() && (
        <div
          css={css`
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 60px;
          `}
        >
          <EditorBtn
            label={<ResetText />}
            onClick={() => setReset(Math.random())}
            css={css`
              width: 150px;
            `}
          />
        </div>
      )}
    </_>
  );
};

export default Matching;
