/** @jsx jsx */

import {
  Fragment as _,
  useState,
  useEffect,
  useRef,
  useLayoutEffect,
  useContext,
  RefObject,
} from 'react';
import { css, jsx } from '@emotion/core';
import { useSelector, useDispatch } from 'react-redux';
import getBlockById from 'services/apiHelpers/getBlockById';
import { createContext } from 'react';
import { Flexbox } from '@chameleoncreator/creator-ui';
import getStore from 'services/getStore';
import getCurrentTopic from 'services/apiHelpers/getCurrentTopic';
import Wrapper from './Wrapper';
import HiddenBackButton from './HiddenBackButton';
import BackgroundImage from './Background';
import next from './next';
import updateReadOnlyText from 'services/updateReadOnlyText';
import pubSub from 'services/pubSub';
import { createSelector } from 'reselect';
import headingsStyles from './headingsStyles';
import { MenuContainer } from './MenuContainer';
import { State } from 'store/store';
import { controlAudioAction } from 'actions/controlAudioAction';
import { useHeader } from 'services/useHeader';
import getTopics from 'services/apiHelpers/getTopics';
import { getIsFirstInteraction } from 'services/getIsFirstInteraction';
import { ModuleContext } from 'components/Module/Module_v2';
import { InfoBubbleContainer } from 'components/Editor/EditorUtilities/UI/Bubble';
import { moduleNavigationActions } from 'components/NavigationSettings/ModuleNavigation';
import { updateInteractionsOpenAction } from 'actions/updateInteractionsOpenAction';
import { buttonLayouts } from './buttonLayouts';
import { updateGlobalFontStylesFromTheme } from 'styles/updateGlobalFontStylesFromTheme';
import './highlightActiveElement';
import { Orbs, getAnimatedOrbsDefaultValues } from 'components/Module/Orbs';

const store = getStore();
const renderCount = {};

const AUDIO_FILE_DEFAULT =
  'https://freesound.org/data/previews/542/542450_5674468-lq.mp3';

export const InteractionContext = createContext<any>(undefined);

const getInteractionsOpen = createSelector(
  (state: any) => state.interactionsOpen,
  interactionsOpen => interactionsOpen
);

const getHiddenBackButton = (
  section: HTMLElement,
  id: string
): HTMLButtonElement => {
  const hiddenBackButton = section?.querySelector(
    `#hidden-back-button-${id}`
  ) as HTMLButtonElement;

  return hiddenBackButton;
};
//

const getHiddenMenuButton = (
  section: HTMLElement,
  id: string
): HTMLButtonElement => {
  const hiddenMenuButton = section?.querySelector(
    `#hidden-button-menu-${id}`
  ) as HTMLButtonElement;

  return hiddenMenuButton;
};

const ContainerLogical = (props: any) => {
  const moduleContext = useContext(ModuleContext);
  const { exit, setComplete, setCompleteAndExit, resetModule } = moduleContext;

  let {
    id,
    name,
    style,
    children,
    lockDown,
    includeAudio = false,
    audioSrc,
    autoPlay = false,
    stopAudioWhenModalIsOpened = true,
    controls = true,
    delay = 1,
    autoOpenNextSection = false,
    //
    backgroundStyle,
  } = props;

  const {
    primaryOrbColor,
    secondaryOrbColor,
    tertiaryOrbColor,
    orbOpacity,
    orbBlurStrength,
    orbSpeed,
  } = getAnimatedOrbsDefaultValues(props);

  const { focusedNodeId, currentInteraction, theme } = useSelector(
    (state: State) => {
      return state;
    }
  );

  const dispatch = useDispatch();

  // check if nextInteractionMap is in 'order'
  // for the moment we'll use this dirty check
  const isFirstInteraction = getIsFirstInteraction(getTopics(), id);

  const [tabbableElements, setTabbableElements] = useState<
    HTMLElement[] | undefined
  >();

  const lastTabbableElement = tabbableElements?.length
    ? tabbableElements[tabbableElements.length - 1]
    : null;

  const hasNavigationButton = lastTabbableElement?.id?.includes(
    'navigation-button'
  );

  let {
    sId,
    background = undefined,
    backgroundImage = undefined,
    backgroundImageOpacity = 100,
    backgroundBlur = 0,
    src = undefined,
    textColor = style.foreground,
  } = getBlockById(id).props as any;

  const {
    backgroundGradient,
    backgroundSecondaryColor,
    backgroundGradientAngle = 0,
    fullWidth = false,
  } = props;

  let buttonPosition = {
    left: 'start',
    center: 'center',
    right: 'end',
  }[props?.buttonPosition || 'center'];

  const linearGradient = backgroundGradient
    ? `linear-gradient(${background}, ${backgroundSecondaryColor})`
    : 'none';

  let interactionsOpen = useSelector(getInteractionsOpen);
  let isOpen = props.isOpen || interactionsOpen.includes(id);

  // dirty gotta try and cover for legacy backgroundImage
  if (src) {
    backgroundImage = `url('${src}') center / cover no-repeat`;
  }

  const section: any = useRef<HTMLElement>(null);
  const selected = id === focusedNodeId;

  const header = useHeader();
  const { headerStyle } = header.props;

  const { backButton, topicMenu } = header[headerStyle].props;

  let headerHeight = header[headerStyle].props.headerHeight;

  if (window.dev() && !isFirstInteraction) {
    headerHeight = 70;
  }

  const currentTopic = getCurrentTopic();

  const parsedTextColor =
    textColor ||
    (currentTopic && getBlockById(currentTopic.id)?.props?.textColor) ||
    theme.textColor ||
    '';

  const { fontSize = 16, lineHeight = 1.5, headings } = theme || {};

  const { h1, h2, h3, h4, h5, h6 } = headings || {};

  const contentContainer = useRef<HTMLDivElement>(null);

  const tabDirection = useRef<'next' | 'prev' | undefined>();
  const trappingFocusMessage = useRef<HTMLSpanElement>();

  useLayoutEffect(() => {
    if (!section.current) {
      section.current = document.querySelector(`#i${id}`);
    }

    !window.dev() && updateReadOnlyText(section.current);
  }, [parsedTextColor]);

  useEffect(() => {
    if (!section?.current) {
      return;
    }

    const hiddenBackButton = getHiddenBackButton(section.current, id);

    if (!hiddenBackButton) {
      return;
    }

    const onKeyDown = (e: any) => {
      const { key, shiftKey } = e;

      if (key === 'Tab' && shiftKey) {
        e.preventDefault();
        //

        tabDirection.current = 'prev';

        if (trappingFocusMessage.current) {
          trappingFocusMessage.current.focus({ preventScroll: true });
          //
          tabDirection.current = undefined;
        }
      }
    };

    hiddenBackButton.addEventListener('keydown', onKeyDown);

    return () => {
      hiddenBackButton.removeEventListener('keydown', onKeyDown);
    };
  }, [open]);

  useEffect(() => {
    if (!section?.current) {
      return;
    }

    const onKeyDown = (e: any) => {
      const { key, shiftKey } = e;

      if (key === 'Tab' && !shiftKey) {
        e.preventDefault();

        const hiddenBackButton = getHiddenBackButton(section.current, id);
        const hiddenMenuButton = getHiddenMenuButton(section.current, id);

        if (hiddenBackButton && !hiddenBackButton.disabled) {
          //
          hiddenBackButton.focus({ preventScroll: true });
        } else {
          hiddenMenuButton ? hiddenMenuButton.focus() : section.current.focus();
        }
      }
    };

    lastTabbableElement?.addEventListener('keydown', onKeyDown);

    return () => {
      lastTabbableElement?.removeEventListener('keydown', onKeyDown);
    };
  }, [lastTabbableElement]);

  useEffect(() => {
    if (!lockDown) {
      return;
    }
  });

  useEffect(() => {
    if (!section.current) {
      return;
    }

    let tick = setInterval(() => {
      if (!section.current) {
        return;
      }
      //
      var scrollBarWidth = window.innerWidth - document.body.clientWidth;

      if (scrollBarWidth > 0) {
        clearInterval(tick);
        // if scrollBarWidth calcs to greater than 35
        // then wtf. wrong things are happening. don't update the padding
        // but if we're within that range. . .
        if (scrollBarWidth <= 35) {
          section.current.style.paddingLeft = `${scrollBarWidth}px`;
        }
      }
    }, 1000 / 12);

    return () => {
      clearInterval(tick);
    };
  }, []);

  useEffect(() => {
    if (window.dev() || !contentContainer.current) {
      return;
    }
    //
    // this is gonna get put someplace else
    // make it part of the hashChange maybe
    // but needs reference to the interaction sId/id
    //
    const anchorNavNextExit = e => {
      const elementsUnderClick = Array.from(
        document.elementsFromPoint(e.clientX, e.clientY)
      );

      const a = elementsUnderClick.find(el => el.tagName.toLowerCase() === 'a');

      const href = a?.getAttribute('href');

      if (!href) {
        return;
      }

      const {
        NEXT,
        EXIT,
        SET_MODULE_COMPLETE,
        SET_MODULE_COMPLETE_AND_EXIT,
        RESET_MODULE,
      } = moduleNavigationActions;

      const aNext = next(sId, id);

      if ([NEXT, EXIT].includes(href)) {
        e.preventDefault();

        aNext(href);
        return;
      }

      if (
        [
          SET_MODULE_COMPLETE,
          SET_MODULE_COMPLETE_AND_EXIT,
          RESET_MODULE,
        ].includes(href)
      ) {
        //
        e.preventDefault();

        href === SET_MODULE_COMPLETE && setComplete();
        href === SET_MODULE_COMPLETE_AND_EXIT && setCompleteAndExit();
        href === RESET_MODULE && resetModule();
        return;
      }
    };

    contentContainer.current.addEventListener('click', anchorNavNextExit);

    return () => {
      contentContainer.current &&
        contentContainer.current.removeEventListener(
          'click',
          anchorNavNextExit
        );
    };
  }, []);

  useEffect(() => {
    if (window.dev()) {
      const data = includeAudio
        ? {
            id,
            audioSrc,
            autoPlay: window.dev() ? false : autoPlay,
            controls,
            delay,
          }
        : null;

      dispatch(controlAudioAction(data));
    }
  }, [includeAudio, audioSrc]);

  useEffect(() => {
    if (window.dev()) {
      return;
    }

    const sectionInViewSubscriber = pubSub.subscribe(
      'SECTION_IN_VIEW',
      ({ sectionInView }) => {
        if (sectionInView.id === id) {
          const data = includeAudio
            ? {
                id,
                audioSrc,
                autoPlay: window.dev() ? false : autoPlay,
                controls,
                delay,
                pause: false,
              }
            : null;

          dispatch(controlAudioAction(data));
        }
      }
    );

    return () => {
      sectionInViewSubscriber();
    };
  }, []);

  useEffect(() => {
    if (autoOpenNextSection) {
      store.dispatch(updateInteractionsOpenAction(id));
    }
  }, []);

  useEffect(() => {
    const tabbableElements = Array.from(
      section.current.querySelectorAll(
        'a[href], button, input:not([type="hidden"]), select, textarea, area[href], iframe, summary, [contenteditable="true"], [tabindex]:not([tabindex="-1"], span)'
      )
    ) as HTMLElement[];

    if (!tabbableElements) {
      return;
    }

    const lastTabbableElement = tabbableElements[tabbableElements.length - 1];

    // if the lastTabbableElement IS NOT a nav buttons in the ' footer '
    // we push trappingFocusMessage into tabbableElements to ' take its place '
    if (!lastTabbableElement?.id?.includes('navigation-button')) {
      if (trappingFocusMessage.current) {
        tabbableElements.push(trappingFocusMessage.current);
      }
    }

    setTabbableElements(tabbableElements as HTMLElement[]);
  }, [open]);

  let isLocked = lockDown;

  // since the background gets set on the style on creation
  // kill it if we have a ' local ' background color
  // goal would be to remove it from the factory. but it's legacy from wizard
  // WINDING on down. . .
  const overideStyleBackground = props.hasOwnProperty('background')
    ? {
        background: background || '',
      }
    : {};

  if (!renderCount[id]) {
    renderCount[id] = 0;
  }

  // need to further work on all the concepts going on with the headerHeight
  // firstInteraction checking
  // responsive aspect etc
  // right now it's being spread over a couple places which feels super gross
  // need to bring it all under some unified solution

  let paddingTop = headerHeight;

  if (!isFirstInteraction && headerStyle === 'graphic') {
    paddingTop = 70;
  }

  if (screen.availWidth <= 480) {
    paddingTop = 50;
  }

  return (
    <InteractionContext.Provider
      value={{
        ...props.data,
        id,
        textColor: parsedTextColor,
        stopAudioWhenModalIsOpened,
        next: next(sId, id),
        isLocked,
        children,
      }}
    >
      {/* @ts-ignore */}
      <Flexbox
        as="section"
        id={`i${id}`}
        css={css`
          ${!isOpen &&
            `
            display: none;
          `}

          ${updateGlobalFontStylesFromTheme({
            fontSize,
            lineHeight,
            parsedTextColor,
            headingsStyles() {
              return headingsStyles({ h1, h2, h3, h4, h5, h6 });
            },
          })}

          [id*='content-container'] > div:last-child {
            ${window.dev()
              ? `margin-${
                  {
                    start: 'left',
                    end: 'right',
                  }[buttonPosition]
                }: -9px;`
              : ''}

            > div {
              ${buttonLayouts(buttonPosition)}
            }
          }
        `}
        style={{
          position: 'relative',
          overflowX: 'hidden',
          ...{
            ...style,
            ...overideStyleBackground,
            ...(backgroundGradient && {
              background: `linear-gradient(${backgroundGradientAngle}deg, ${backgroundSecondaryColor}, ${background})`,
            }),
          },
        }}
        py={4}
        flexDirection="column"
        justifyContent="center"
        alignItems="center"
        minHeight={'100vh'}
        width={['100%']}
        paddingTop={paddingTop}
        data-section
        data-section-text-color={parsedTextColor}
        aria-label={`${props.name ? `${props.name}` : ''}${
          props.sectionLabel
        }`}
        tabIndex={-1}
      >
        {[undefined, 'backgroundImage'].includes(backgroundStyle) &&
          backgroundImage && (
            <BackgroundImage
              key={backgroundImage}
              backgroundImage={backgroundImage}
              backgroundImageOpacity={backgroundImageOpacity}
              backgroundBlur={backgroundBlur}
            />
          )}

        {backgroundStyle === 'animatedOrbs' && (
          <Orbs
            {...{
              orbsFor: 'interaction',
              primaryOrbColor,
              secondaryOrbColor,
              tertiaryOrbColor,
              orbOpacity,
              orbBlurStrength,
              orbSpeed,
            }}
          />
        )}

        {/* let's not even render this puppy if isFirstInteraction */}
        {!window.dev() && backButton && !isFirstInteraction ? (
          <HiddenBackButton id={id} />
        ) : null}

        {topicMenu && <MenuContainer id={id} rootSelector="#menu-portal" />}

        {/* @ts-ignore */}
        <Flexbox
          ref={contentContainer}
          id={`i${id}-content-container`}
          position="relative"
          flexDirection="column"
          justifyContent="center"
          width={['100%']}
          maxWidth={['100%', null, fullWidth ? '100%' : '1100px']}
          css={css({
            ...(includeAudio &&
              audioSrc &&
              controls && {
                '@media (max-width: 768px)': {
                  paddingBottom: 20,
                },
              }),
          })}
        >
          {children}
        </Flexbox>

        {!hasNavigationButton ? (
          <span
            ref={
              (trappingFocusMessage as unknown) as RefObject<HTMLSpanElement>
            }
            style={{
              padding: '10px 20px',
              opacity: 0,
              transition: 'opacity 0.3s',
              borderRadius: 8,
              border: `1px solid ${parsedTextColor}`,
              color: parsedTextColor,
              fontSize: 14,
            }}
            tabIndex={0}
            onBlur={e => {
              e.currentTarget.style.opacity = '0';
            }}
            onFocus={e => {
              e.currentTarget.style.opacity = '1';
            }}
          >
            Tab to return to top. Shift + Tab to return to content.
          </span>
        ) : null}

        {window.dev() && (
          <Wrapper
            id={id}
            focusedNodeId={focusedNodeId}
            headerHeight={headerHeight}
          />
        )}
      </Flexbox>

      <InfoBubbleContainer />
    </InteractionContext.Provider>
  );
};

export default ContainerLogical;
