/** @jsx jsx */

import React, { Fragment as _, useState, useEffect } from 'react';
import { keyframes } from 'emotion';
import { css, jsx } from '@emotion/core';
import { useSelector, useDispatch } from 'react-redux';
import { colors } from 'styles/variables';
import { EditWrapper1 } from './EditWrappers';
import LoadingSpinner from 'components/UI/LoadingSpinner';
import FullScreenIcon from 'assets/FullScreenIcon.svg';
import isAndroidSamsung from 'services/isAndroidSamsung';
import isiOS from 'services/isiOS';
import CloseFullscreenEmbed from 'assets/CloseFullscreenEmbed.svg';
import { hideHeaderAction } from 'actions/hideHeaderAction';

const { lPink, lGrey0, lGrey2 } = colors;

const frameSelected = keyframes`
  from {
    width: 50%;
    height: 50%;
  }
  to {
    width: 75%;
    height: 75%;
  }
`;

const minHeight = '50vh';
const getMinHeight = fullWindowHeight => {
  return fullWindowHeight ? 'calc(100vh - 250px)' : minHeight;
};

const IFrame = ({
  src,
  fullWindowHeight,
  fullscreenMode = false,
  aspectRatio,
}: {
  src: string;
  fullWindowHeight?: string;
  fullscreenMode?: boolean | undefined;
  aspectRatio?: string;
}) => {
  const [loaded, setLoaded] = useState(false);

  return (
    <_>
      <iframe
        src={src}
        css={css`
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          // ${loaded && `min-height: ${getMinHeight(fullWindowHeight)};`}

          ${
            !aspectRatio
              ? loaded && `min-height: ${getMinHeight(fullWindowHeight)};`
              : `aspect-ratio: ${aspectRatio};`
          }

          border: none;

          ${!loaded &&
            `
            width: 0;
            height: 0;
          `}

          ${fullscreenMode &&
            `
            width: 100vw;
            height: 100vh;
          `}
        `}
        onLoad={(e: any) => {
          setLoaded(true);
        }}
      ></iframe>
      {!loaded && (
        <div
          css={css({
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            width: '100%',
            height: '100%',

            ...(!aspectRatio
              ? { minHeight: getMinHeight(fullWindowHeight) }
              : { aspectRatio }),
          })}
        >
          <LoadingSpinner />
        </div>
      )}
    </_>
  );
};

const Embed = props => {
  const {
    id,
    src: clientSrc = 'https://www.chameleoncreator.com',
    fullWindowHeight = false,
    mediaParentId = null,
    dropShadow = 1,
    borderRadius = 20,
    includeBorder = 1,
    borderColor = lGrey2,
    includeAspectRatio = 0,
  } = props;

  const src =
    (window as any).source !== 'scorm'
      ? `https://chameleoncreatorembed.com/?target=${encodeURIComponent(
          clientSrc
        )}`
      : clientSrc;

  //
  let borderSize = includeBorder ? 1 : 0;

  let aspectRatio = (props.aspectRatio || [1, 1]).join('/');

  const [iFrameHovered, setIFrameHovered] = useState<boolean>(false);
  const [fullscreenMode, setFullscreenMode] = useState<boolean>(false);

  const focusedNodeId = useSelector((state: any) => state.focusedNodeId);
  const dispatch = useDispatch();

  const selected = id === focusedNodeId;

  //
  const allowFullScreen = window.dev && !window.dev() && props.allowFullScreen;

  const hideHeader = (hide: boolean) => {
    dispatch(hideHeaderAction(hide));
  };

  useEffect(() => {
    if (
      isAndroidSamsung() ||
      isiOS() ||
      window.matchMedia('(hover: none)').matches
    ) {
      // hover unavailable
      setIFrameHovered(true);
    } else {
      // console.log('HOVER IS AVAILABLE');
    }
    //
  }, []);

  useEffect(() => {
    const exitFullscreen = e => {
      if (e.key === 'Escape') {
        hideHeader(false);
        document.removeEventListener('keydown', exitFullscreen);
        setFullscreenMode(false);
      }
    };

    if (fullscreenMode) {
      hideHeader(true);
      document.addEventListener('keydown', exitFullscreen);
    } else {
      hideHeader(false);
      document.removeEventListener('keydown', exitFullscreen);
    }

    return () => {
      //
    };
  }, [fullscreenMode]);

  return (
    <div
      css={css({
        position: 'relative',
        width: '100%',
        height: '100%',

        ...(!!!includeAspectRatio
          ? { minHeight: getMinHeight(fullWindowHeight) }
          : { aspectRatio }),

        outline: `${borderSize}px solid ${borderColor}`,
        boxShadow: dropShadow
          ? '5px 10px 20px 10px rgba(0, 0, 0, 0.05)'
          : 'none',

        borderRadius,
        overflow: 'hidden',

        ...(fullscreenMode && {
          position: 'fixed',
          left: 0,
          top: 0,
          width: '100vw',
          height: '100vh',
          background: '#fff',
          zIndex: 2147483647,
        }),
      })}
      onMouseOver={() => {
        setIFrameHovered(true);
      }}
      onMouseOut={() => {
        setIFrameHovered(false);
      }}
    >
      {window.dev() ? (
        <EditWrapper1 id={id} mediaParentId={mediaParentId} selected={selected}>
          <IFrame src={src} fullWindowHeight={fullWindowHeight} />
        </EditWrapper1>
      ) : (
        <IFrame
          src={src}
          fullWindowHeight={fullWindowHeight}
          fullscreenMode={fullscreenMode}
          aspectRatio={aspectRatio}
        />
      )}

      {!!allowFullScreen &&
        (!fullscreenMode ? (
          <button
            css={css({
              border: 'none',
              outline: 'none',
              position: 'absolute',
              display: 'flex',
              alignItems: 'center',
              left: 0,
              bottom: '0px',
              width: '100%',
              height: 55,
              padding: 15,
              background: `rgba(7, 9, 17, ${iFrameHovered ? 0.7 : 0})`,
              transform: `translateY(${iFrameHovered ? 0 : '55px'})`,
              transition: 'transform 0.25s ease-out',
              color: '#fff',
              cursor: 'pointer',
            })}
            onClick={() => {
              setFullscreenMode(true);
            }}
          >
            <img src={FullScreenIcon} style={{ margin: '0 15px 0 0' }} />
            Full screen
          </button>
        ) : (
          <button
            css={css({
              border: 'none',
              outline: 'none',
              position: 'absolute',
              right: 35,
              top: 20,
              width: 35,
              height: 35,
              background: `url('${CloseFullscreenEmbed}') no-repeat`,
              cursor: 'pointer',

              '&:hover': {
                opacity: 0.5,
              },
            })}
            onClick={() => {
              setFullscreenMode(false);
            }}
          />
        ))}
    </div>
  );
};

export default Embed;
