import { useState, useEffect, useRef, useCallback, useContext } from 'react';
import { css, jsx } from '@emotion/core';
import 'services/ie11closestPolyfill';
import pubSub from 'services/pubSub';
import { useSelector } from 'react-redux';
import { State } from 'store/store';
import { InteractionContext } from 'components/Layouts/ContainerLogical';
import { speechSynthesisCancel } from 'services/speechSynthesisCancel';
import isIOS from 'services/isiOS';

let speechSynth;
if ('speechSynthesis' in window) {
  speechSynth = new SpeechSynthesisUtterance();
}

console.log({ speechSynth });

