export const TextToSpeech = props => {
  const [color, setColor] = useState<string | undefined>(undefined);
  const [canPlay, setCanPlay] = useState<boolean>(true);

  const voice = useSelector((state: State) => {
    if (!speechSynth) {
      return null;
    }

    const voices = window.speechSynthesis.getVoices();

    //
    const UK_English_Male_iOS = voices.findIndex(voice => {
      return voice?.name?.toLowerCase() === 'daniel';
    });

    const voiceSelection = voices.find(voice => {
      return voice.name === state.theme.textToSpeechVoice;
    });

    // this isn't 'right' when defaulting to voices[0]
    // entry 0 isn't really a default is it
    // we will update later when we've established our refined pattern
    // for voice selection and the associated defaults for each platform
    const voiceFallback =
      voices[isIOS() && UK_English_Male_iOS > -1 ? UK_English_Male_iOS : 0];

    return voiceSelection || voiceFallback;
  });

  const chameleonText = useRef<HTMLElement>();
  const textToSpeechBtn = useRef<HTMLDivElement>(null);

  const onClick = useCallback(
    e => {
      if (!speechSynth) {
        return;
      }

      e.preventDefault();
      e.stopPropagation();

      speechSynthesisCancel();

      if (chameleonText.current && canPlay) {
        //

        // this approach was somewhat problems
        // reading '.' periods as ' dot ' !!!
        // const doc = new DOMParser().parseFromString(content, 'text/html');
        // speechSynth.text = doc.body.innerText || content;

        const text = chameleonText.current.innerText || '';
        speechSynth.text = text;

        speechSynth.voice = voice;
        window.speechSynthesis.speak(speechSynth);
      }

      setCanPlay(canPlay => {
        return !canPlay;
      });
    },
    [canPlay, voice?.name]
  );

  useEffect(() => {
    if (!textToSpeechBtn.current) {
      return;
    }

    chameleonText.current = textToSpeechBtn.current.closest(
      '[class*="chameleon-text"]'
    ) as HTMLElement;
    const chameleonTextP = chameleonText.current?.querySelector(
      'p, h1, h2, h3, h4, h5, h6, li'
    );

    if (chameleonTextP) {
      const cssObj = window.getComputedStyle(chameleonTextP, null);
      setColor(cssObj.color);
    }
  });

  useEffect(() => {
    if (!speechSynth) {
      return;
    }

    const onStart = e => {
      // console.log('speechSynth starts speeching...');
    };
    const onEnd = e => {
      setCanPlay(true);
    };

    speechSynth.addEventListener('start', onStart);
    speechSynth.addEventListener('end', onEnd);

    return () => {
      speechSynth.removeEventListener('start', onStart);
      speechSynth.removeEventListener('end', onEnd);
      speechSynthesisCancel();
    };
  }, []);

  useEffect(() => {
    const pS = pubSub.subscribe('SECTION_IN_VIEW', () => {
      setCanPlay(true);
    });

    return pS;
  }, []);

  return (
    <div
      key={`text-to-speech-${color}-${canPlay.toString()}`}
      ref={textToSpeechBtn}
      css={css({
        border: 'none',
        outline: 'none',
        width: 24,
        height: 24,
        margin: '5px 0 0 0',
        borderRadius: '50%',
        background: 'transparent',
        fontSize: 7,
        cursor: 'pointer',

        transition: 'all 0.2s',

        ':hover': {
          opacity: 0.8,
        },

        ':focus-visible': {
          boxShadow: `0 0 0 3px ${color}`,
        },

        zIndex: 99999,
      })}
      onClick={onClick}
      onKeyDown={e => {
        if ([' ', 'Enter'].includes(e.key)) {
          onClick(e);

          requestAnimationFrame(() => {
            textToSpeechBtn.current?.focus();
          });
        }
      }}
      role="button"
      tabIndex={0}
      aria-label="Click here to have this passage of text read aloud. Click again to stop narration"
    >
      <PlayBtn play={canPlay} color={color} />
    </div>
  );
};
