const EmptySection = () => {
  const moduleHasInteractions = useSelector((state: State) => {
    return Object.values(state.blocks)
      .filter(b => {
        return b?.meta?.subType === 'Interaction';
      })
      .some(i => {
        return i?.children?.length;
      });
  });

  if (window.dev() && !moduleHasInteractions) {
    return <DashBoard />;
  }

  return (
    <div
      style={{
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
      }}
    >
      {!window.dev() ? (
        <_>
          <div
            style={{
              width: '50%',
              height: '50%',
              background: `url('${theresNothingHereYet}') no-repeat`,
              backgroundSize: 'contain',
              backgroundPosition: 'center',
            }}
          />
          <div
            style={{
              position: 'relative',
              top: '30px',
              textAlign: 'center',
              color: '#0094ff',
              fontWeight: 300,
            }}
          >
            There are no interactions here yet
          </div>
        </_>
      ) : (
        <_>
          <div
            style={{
              position: 'absolute',
              width: '50%',
              height: '50%',
              background: `url('${addInteractionBKG}') no-repeat`,
              backgroundSize: 'contain',
              backgroundPosition: 'center',
            }}
          />
          <button
            style={{
              all: 'unset',
              position: 'relative',
              width: 60,
              height: 60,
            }}
            onClick={e => {
              parent.setNewSectionWizard && parent.setNewSectionWizard(true);
            }}
          >
            <AddInteractionBtn />
          </button>
          <div
            style={{
              position: 'relative',
              top: '15px',
              textAlign: 'center',
              color: '000',
              fontWeight: 500,
            }}
          >
            Add an interaction.
          </div>
        </_>
      )}
    </div>
  );
};

const AddInteractionBtn = () => {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="60"
      height="60"
      viewBox="0 0 60 60"
      css={css({
        position: 'relative',
        cursor: 'pointer',
        fill: 'none',
        ':hover': {
          '.actual-btn': {
            fill: '#00D9D3',
          },
        },
      })}
    >
      <rect
        className="actual-btn"
        width="60"
        height="60"
        rx="12"
        css={css({
          fill: '#1D2026',
          transition: 'fill 0.2s',
        })}
      />
      <rect x="28" y="18" width="4" height="24" fill="white" />
      <rect
        x="18"
        y="32"
        width="4"
        height="24"
        transform="rotate(-90 18 32)"
        fill="white"
      />
    </svg>
  );
};

export default EmptySection;
