import { createBlock } from 'factories/block';
import { repeat } from './interaction';
import { createLayout } from 'factories/layout';
import { colors } from 'styles/variables';
import { createMediaBlock } from './media';
import { Block, cloneBlock, Blocks } from '@chameleoncreator/creator-app';
import { flatten } from 'factories/utils';
import { createContinueBtn } from './continueBtn';
import { createEditableBtn } from './editableBtn';
import { getSId } from '../utils';
import { getPlaceholderText } from './text';

const { lGrey0, lGrey2 } = colors;

const createClickAndRevealBlock = props => {
  return createBlock('ClickAndReveal', [
    createMediaBlock({
      src: undefined,
      child: 1,
      fit: props?.isLandingPage ? 'contain' : 'cover',
      useBackgroundControls: false,
    }),
    createBlock('Text', [], {
      props: {
        useBackgroundControls: false,
        content: 'Example text',
        className: 'text-align-center',
      },
    }),
    createBlock('Content', [
      createMediaBlock({ name: 'ColumnRowA', child: 0 }),
      createMediaBlock({ name: 'ColumnRowB', child: 1 }),
      createEditableBtn({
        type: 'ClickAndRevealContentButton',
        label: 'Continue',
      }),
    ]),
  ]);
};

export const createClickAndRevealsBlock = (
  count: number,
  isLandingPage?: boolean
) => {
  return createBlock('ClickAndReveals', [
    ...repeat(() => createClickAndRevealBlock({ isLandingPage }), count),
  ]);
};

export const addClickAndReveal = (
  block: Block,
  position: number
): [Block, Blocks?] => {
  if (block.type === 'ClickAndReveals') {
    const clickAndRevealBlock = createClickAndRevealBlock({});

    const clickAndRevealBlockRef = { id: clickAndRevealBlock.id, type: 'Ref' };
    const [newBlocks] = flatten(clickAndRevealBlock);

    const nextBlock = cloneBlock(block);
    (nextBlock.children as Block[]).splice(position, 0, clickAndRevealBlockRef);

    return [nextBlock, newBlocks];
  }
  return [block, undefined];
};

export const removeClickAndReveal = (
  block: Block,
  position: number
): [Block, Blocks?] => {
  if (block.type === 'ClickAndReveals') {
    const clickAndRevealBlock = createClickAndRevealBlock({});

    const [newBlocks] = flatten(clickAndRevealBlock);

    const nextBlock = cloneBlock(block);
    (nextBlock.children as Block[]).splice(position, 1);

    return [nextBlock, newBlocks];
  }
  return [block, undefined];
};

const contentForColumn = (count: number, isLandingPage?: boolean) => (
  i: number
) => {
  return [
    () =>
      createMediaBlock({
        child: 0,
        textPlaceholderContent: getPlaceholderText('h2', 'heading-two'),
        className: 'text-align-center',
      }),
    () =>
      createBlock('InteractionColumn', [
        createClickAndRevealsBlock(count, isLandingPage),
        createMediaBlock({
          child: 0,
          isAdditionalMedia: true,
        }),
      ]),
  ][i];
};

export const createClickAndRevealInteractionBlock = props => {
  const {
    backgroundColor,
    clickAndRevealPanelTextColor = lGrey0,
    clickAndRevealLabelTextColor = lGrey0,
    width = 300,
    height = 220,
    borderRadius = 20,
    borderColor = lGrey2,
    layout = 'One column reversed',
    lockDown = 0,
    style,
  } = props as any;

  const useGlobalNavigation = props.useGlobalNavigation || false;
  const isLandingPage = props.isLandingPage || false;

  return createBlock(
    'Interaction',
    [
      createLayout(contentForColumn(props.answerCount, isLandingPage), 0, true),
      createBlock('SingleColumnLayout', [
        createBlock('Column', [createContinueBtn(style)], {
          props: {
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            flexWrap: 'wrap',
          },
          meta: {
            subType: 'ButtonBlock',
          },
        }),
      ]),
    ],
    {
      props: {
        name: props.name,
        sId: getSId('interaction'),
        style: props.style,
        backgroundColor,
        clickAndRevealPanelTextColor,
        clickAndRevealLabelTextColor,
        borderRadius,
        borderColor,
        width,
        height,
        layout,
        lockDown,
        ...(useGlobalNavigation && {
          useGlobalNavigation,
        }),
        ...(isLandingPage && {
          isLandingPage,
        }),
      },
      meta: {
        subType: 'ClickAndReveal',
        layout: 'OneColumn',
      },
    }
  );
};

export const create = createClickAndRevealInteractionBlock;
export const interactionConfig = {};
