/** @jsx jsx */

import React, { Fragment as _, useState, useEffect } from 'react';

import { keyframes } from 'emotion';
import { css, jsx } from '@emotion/core';
import { useSelector, useDispatch } from 'react-redux';
import { colors } from 'styles/variables';
import { EditWrapper1 } from './EditWrappers';
import LoadingSpinner from 'components/UI/LoadingSpinner';
import FullScreenIcon from 'assets/FullScreenIcon.svg';
import isAndroidSamsung from 'services/isAndroidSamsung';
import isiOS from 'services/isiOS';
import CloseFullscreenEmbed from 'assets/CloseFullscreenEmbed.svg';
import { hideHeaderAction } from 'actions/hideHeaderAction';
import imagePlaceholder from 'assets/Long Rectangle.svg';
import clickPreviewToSeeOutput from 'assets/clickPreviewToSeeOutput.png';

const { lPink, lGrey0, lGrey2 } = colors;

const frameSelected = keyframes`
  from {
    width: 50%;
    height: 50%;
  }
  to {
    width: 75%;
    height: 75%;
  }
`;

const minHeight = '50vh';
const getMinHeight = fullWindowHeight => {
  return fullWindowHeight ? 'calc(100vh - 250px)' : minHeight;
};

const IFrame = ({
  src,
  fullWindowHeight,
  fullscreenMode = false,
}: {
  src: string;
  fullWindowHeight?: string;
  fullscreenMode?: boolean | undefined;
}) => {
  const [loaded, setLoaded] = useState(false);

  return (
    <_>
      <iframe
        src={src}
        css={css`
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          ${loaded && `min-height: ${getMinHeight(fullWindowHeight)};`}
          border: none;

          ${!loaded &&
            `
            width: 0;
            height: 0;
          `}

          ${fullscreenMode &&
            `
            width: 100vw;
            height: 100vh;
          `}
        `}
        onLoad={(e: any) => {
          setLoaded(true);
        }}
      ></iframe>
      {!loaded && (
        <div
          css={css({
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            width: '100%',
            height: '100%',
            minHeight: getMinHeight(fullWindowHeight),
          })}
        >
          <LoadingSpinner />
        </div>
      )}
    </_>
  );
};

const PDF = props => {
  const {
    id,
    src,
    imgSrc,
    displayInline,
    fullWindowHeight = false,
    mediaParentId = null,
    //
    cardColor = '#fff',
    cardOutline = '#060709',
    includeImage = true,
    description = '',
    cardBorderRadius = 20,
    includeText = true,
    includeShadow = true,
    focusColor = '#000',
    //
    children,
  } = props;

  // console.log('PDF :', src);
  // http://developer.amd.com/wordpress/media/2013/02/NPAR07_IllustrativeRenderingInTeamFortress2.pdf

  const img = children[0];
  const PDFThumbnail = children[0].props.src || imagePlaceholder;

  const [iFrameHovered, setIFrameHovered] = useState<boolean>(false);
  const [fullscreenMode, setFullscreenMode] = useState<boolean>(false);

  const focusedNodeId = useSelector((state: any) => state.focusedNodeId);
  const dispatch = useDispatch();

  const selected = id === focusedNodeId;

  //
  const allowFullScreen = window.dev && !window.dev() && props.allowFullScreen;

  const hideHeader = (hide: boolean) => {
    dispatch(hideHeaderAction(hide));
  };

  useEffect(() => {
    if (
      isAndroidSamsung() ||
      isiOS() ||
      window.matchMedia('(hover: none)').matches
    ) {
      // hover unavailable
      setIFrameHovered(true);
    } else {
      // console.log('HOVER IS AVAILABLE');
    }
    //
  }, []);

  useEffect(() => {
    const exitFullscreen = e => {
      if (e.key === 'Escape') {
        hideHeader(false);
        document.removeEventListener('keydown', exitFullscreen);
        setFullscreenMode(false);
      }
    };

    if (fullscreenMode) {
      hideHeader(true);
      document.addEventListener('keydown', exitFullscreen);
    } else {
      hideHeader(false);
      document.removeEventListener('keydown', exitFullscreen);
    }

    return () => {
      //
    };
  }, [fullscreenMode]);

  const openPDF = React.cloneElement(children[0], {
    id,
    mediaParentId,
    selected,
    src: PDFThumbnail,
    pdfSrc: src,
    cardColor,
    cardOutline,
    includeImage,
    description,
    cardBorderRadius,
    includeText,
    includeShadow,
    focusColor,
  });

  if (!displayInline) {
    return openPDF;
  }

  return (
    <div
      css={css({
        position: 'relative',
        width: '100%',
        height: '100%',
        minHeight: getMinHeight(fullWindowHeight),
        outline: `1px solid ${lGrey2}`,
        boxShadow: '5px 10px 20px 10px rgba(0, 0, 0, 0.05)',
        overflow: 'hidden',

        ...(fullscreenMode && {
          position: 'fixed',
          left: 0,
          top: 0,
          width: '100vw',
          height: '100vh',
          background: '#fff',
          zIndex: 2147483647,
        }),
      })}
      onMouseOver={() => {
        setIFrameHovered(true);
      }}
      onMouseOut={() => {
        setIFrameHovered(false);
      }}
    >
      {window.dev() ? (
        <EditWrapper1 id={id} mediaParentId={mediaParentId} selected={selected}>
          <img src={clickPreviewToSeeOutput} style={{ maxWidth: '100%' }} />
        </EditWrapper1>
      ) : (
        <IFrame
          src={src}
          fullWindowHeight={fullWindowHeight}
          fullscreenMode={fullscreenMode}
        />
      )}

      {!!allowFullScreen &&
        (!fullscreenMode ? (
          <button
            style={{
              border: 'none',
              outline: 'none',
              position: 'absolute',
              display: 'flex',
              alignItems: 'center',
              left: 0,
              bottom: '0px',
              width: '100%',
              height: 55,
              padding: 15,
              background: `rgba(7, 9, 17, ${iFrameHovered ? 0.7 : 0})`,
              transform: `translateY(${iFrameHovered ? 0 : '55px'})`,
              transition: 'transform 0.25s ease-out',
              color: '#fff',
              cursor: 'pointer',
            }}
            onClick={() => {
              setFullscreenMode(true);
            }}
          >
            <img src={FullScreenIcon} style={{ margin: '0 15px 0 0' }} />
            Full screen
          </button>
        ) : (
          <button
            css={css({
              '&:hover': {
                opacity: 0.5,
              },
            })}
            style={{
              border: 'none',
              outline: 'none',
              position: 'absolute',
              right: 35,
              top: 75,
              width: 35,
              height: 35,
              background: `url('${CloseFullscreenEmbed}') no-repeat`,
              cursor: 'pointer',
            }}
            onClick={() => {
              setFullscreenMode(false);
            }}
          />
        ))}
    </div>
  );
};

export default PDF;
