import React, {
  Fragment as _,
  useLayoutEffect,
  useEffect,
  useRef,
  useContext,
  cloneElement,
} from 'react';
import { css, jsx } from '@emotion/core';
import { interactionContext as InteractionContext } from './interactionContext';
import CC_Sortation_Placeholder_Panel_Media from 'assets/CC_Sortation Placeholder_Panel Media 4.png';
