// CC_Sortation_Placeholder_Panel_Media.png';

export const SortableImage = props => {
  const context = useContext<any>(InteractionContext);

  const { children } = props;
  const Img = children[0];

  return (
    <div
      css={css({
        img: {
          maxHeight: '150px !important',
        },
      })}
    >
      {cloneElement(Img, {
        placeholder: CC_Sortation_Placeholder_Panel_Media,
      })}
    </div>
  );
};
