import { createBlock } from 'factories/block';
import { Block, cloneBlock, Blocks } from '@chameleoncreator/creator-app';
import { InteractionProps, reverse, repeat } from './interaction';
import { createLayout } from 'factories/layout';
import { colors } from 'styles/variables';
import { flatten } from 'factories/utils';
import { createContinueBtn } from './continueBtn';
import { getSId } from '../utils';
import { createMediaBlock } from './media';
import { getPlaceholderText } from './text';

const { lGrey0 } = colors;

export type CarouselInteractionProps = InteractionProps & {
  numberOfSlides: number;
};

export const createSlideBlock = (): Block => {
  return createBlock('Slide', [
    createBlock('Image', [], {
      props: {
        src: undefined,
        fit: 'contain', // was cover
      },
    }),

    createBlock('Text', [], {
      props: {
        content: 'Example text content',
        className: 'text-align-center',
      },
    }),
  ]);
};

export const createCarouselBlock = (count: number): Block => {
  return createBlock('Carousel', [...repeat(createSlideBlock, count)]);
};

export const removeCarouselSlide = (
  block: Block,
  position: number
): [Block, Blocks?] => {
  if (block.type === 'Carousel') {
    const nextBlock = cloneBlock(block);
    (nextBlock.children as Block[]).splice(position, 1);

    return [nextBlock, undefined];
  }
  return [block, undefined];
};

export const addCarouselSlide = (
  block: Block,
  position: number
): [Block, Blocks?] => {
  if (block.type === 'Carousel') {
    const slideBlock = createSlideBlock();
    const slideBlockRef = { id: slideBlock.id, type: 'Ref' };
    const [newBlocks] = flatten(slideBlock);

    const nextBlock = cloneBlock(block);
    (nextBlock.children as Block[]).splice(position, 0, slideBlockRef);

    return [nextBlock, newBlocks];
  }
  return [block, undefined];
};

const contentForColumn = (slideCount: number) => (i: number) => {
  return [
    () =>
      createMediaBlock({
        child: 0,
        textPlaceholderContent: getPlaceholderText('h1', 'heading-one'),
        className: 'text-align-center',
      }),
    () =>
      createBlock('InteractionColumn', [
        createCarouselBlock(slideCount),
        createMediaBlock({
          child: 0,
          isAdditionalMedia: true,
        }),
      ]),
  ][i];
};

export const createCarouselInteractionBlock = (
  props: CarouselInteractionProps
) => {
  const {
    carouselColor = '#fff',
    carouselTextColor = lGrey0,
    lockDown = 0,
    style,
  } = props as any;

  let columnCount = 0; //  props.columnCount;
  let columnsReversed = true; //  props.columnsReversed;

  return createBlock(
    'Interaction',
    [
      createLayout(
        contentForColumn(props.numberOfSlides),
        columnCount,
        columnsReversed
      ),
      createBlock('SingleColumnLayout', [
        createBlock('Column', [createContinueBtn(style)], {
          props: {
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            flexWrap: 'wrap',
          },
          meta: {
            subType: 'ButtonBlock',
          },
        }),
      ]),
    ],
    {
      props: {
        name: props.name,
        sId: getSId('interaction'),
        style: props.style,
        carouselColor,
        carouselTextColor,
        lockDown,
      },
      meta: {
        subType: 'Carousel',
        layout: `${columnCount === 0 ? 'OneColumn' : 'TwoColumns'}${
          columnsReversed ? 'Reversed' : ''
        }`,
      },
    }
  );
};

export const interactionConfig = {};
